@media only screen and (max-width: 1700px){
    .notFoundingMsg {
        margin: 50px 80px;
        height: 68vh;
    }
}
@media only screen and (max-width: 1400px){
    .notFoundingMsg {
        margin: 50px 80px;
        height: 68vh;
    }
}
@media only screen and (max-width: 780px) {
    .controls .playBtn {
        padding: 15px 17px;
        width: fit-content;
        height: fit-content;
    }

    .AboutPage {
        padding: 41px 20px;
    }
    .Recent_Played_Msg {
        width: 70%;
        height: 75vh;
        overflow: hidden;
        margin-top: 0%;
        /* background-color: #282c34; */
    }
    .Favourites_Songs_Section h2 {
        font-size: 33px;
    }
}

@media only screen and (max-width: 700px) {
    .search_field input {
        width: 100%;
    }

    .plainCard img {
        width: 18%;
    }

    .plainCard .recent-controls {
        width: 17%;
    }

    .controls {
        justify-content: space-between;
        padding: 0px 10px;
    }

    .footer {
        padding: 0px 17px;
    }

    .AboutPage h1{
        font-size: 25px;
    }
    .AboutPage p{
        font-size: 15px;
    }
}


@media only screen and (max-width: 500px){
    .notFoundingMsg {
        margin: 50px 80px;
        height: 68vh;
    }
}
@media only screen and (max-width: 610px) {
    .Music_App_Initial .text_Para {
        width: 80%;
    }
    .Music_App_Initial p {
        text-align: justify;
        font-size: 29px;
    }
    
    .Favourites_Songs_Section h2 {
        font-size: 25px;
    }
}
@media only screen and (max-width: 540px) {
    .AboutPage h1 {
        font-size: 19px;
    }
    .Music_App_Initial {
        height: 100vh;
        align-items: flex-start;
    }
    .Music_App_Initial .text_Para {
        width: 100%;
    }
    .Music_App_Initial h2{
        font-size: 35px;
    }
    .Music_App_Initial p {
        text-align: justify;
        font-size: 19px;
    }
    .Music_App_Initial {
        padding: 0px 20px;
    }
}
@media only screen and (max-width: 420px) {
    .volumeBtn {
        position: absolute;
        top: 2%;
        left: 15px;
    }

    .volumeBtn input {
        width: 90px;
        visibility: visible;
    }
    .Favourites_Songs_Section h2 {
        font-size: 18px;
    }
}


@media only screen and (max-width: 470px) {
    .search_field {
        padding: 40px 15px;
    }

    .search_field button {
        right: -2px;
    }

    .musicNav {
        padding: 25px 12px;
    }

    .Sidebar {
        width: 100%;
    }

    .track p {
        font-size: 13px;
    }

    .plainCard {
        border-radius: 9px;
        width: 94%;
        height: -moz-fit-content;
        height: 57px;
        padding: 6px;
        gap: 30px;
    }

    .plainCard img {
        width: 53%;
        height: 100%;
        border-radius: 6px;
    }

    .plainCard .name-details {
        width: 100%;
        justify-content: center;
        align-items: center;
    }

    .recent-controls button {
        width: 100%;
    }

    .Recent_Played_Msg {
        width: 95%;
    }

    .notFoundingMsg {
        margin: 50px 0px;
        height: 68vh;
    }
}


@media only screen and (max-width: 330px) {
    .recent-played-section h2 {
        font-size: 16px;
    }
    .musicNav .logo label {
        font-size: 21px;
    }
    .musicNav .icon i {
        font-size: 28px;
    }
    .recent-controls button {
        background-color: rgb(239 239 239);
        border: none;
    }
    .recent-controls button::before{
        background: none;
    }
}

@media only screen and (max-width: 290px) {
    .recent-controls button{
        padding: 13px 2px;
    }
    .recent-controls button::before{
        background: none;
    }
    .Favourites_Songs_Section h2 {
        font-size: 13px;
    }
}